/* NAVBAR*/
.navbar
{
  padding:0rem 0.75rem;
  background-color: rgb(2, 2, 2);
}
.navbar-brand
{
  padding: 0;
  margin:0;
}

.navbar-nav > li
{
	padding: 0;
	margin: 0;
}

.navbar-nav > li > a 
{
	padding: 0;
	margin: 0;
    vertical-align: bottom;
}
.navbar-nav > li > p 
{
	padding: 0;
	margin: 0;
	background: yellow;
	vertical-align: bottom;
}